import React from "react";
import About from "../about/About";
import Contact from "../contact/Contact";
import Home from "../home/Home";
// import Navbar from '../navbar/Navbar';
import Foot from "../footer/Foot";
import Service from "../service/Service";
import ProjectT from "../project/ProjectT";

function Connect(props) {
  return (
    <>
      {/* <  Navbar mode={props.mode} togglemode={props.togglemode}/> */}
      <Home mode={props.mode} />
      <Service mode={props.mode} />
      {/* <Project mode={props.mode} /> */}
      <ProjectT mode={props.mode} />

      <About mode={props.mode} />
      <Contact mode={props.mode} />
      <hr
        className={`text-${props.mode === "light" ? "dark" : "light"} mt-5`}
      />
      <Foot mode={props.mode} />
    </>
  );
}

export default Connect;
