import React, { useRef } from "react";
import { useState } from "react";
import emailjs from "@emailjs/browser";
function Contact(props) {
  const border = {
    border: "1px solid blue",
  };
  const form = useRef();
  const [name, setname] = useState("");
  const [email, setemail] = useState("");
  const [contact, setcontact] = useState("");
  const [textarea, settextarea] = useState("");
  const ename = (event) => {
    setname(event.target.value);
  };
  const eemail = (event) => {
    setemail(event.target.value);
  };
  const econtact = (event) => {
    setcontact(event.target.value);
  };
  const etextarea = (event) => {
    settextarea(event.target.value);
  };
  const sendEmail = (e) => {
    e.preventDefault();
    emailjs
      .sendForm(
        "service_epe997h",
        "template_atxwwcg",
        form.current,
        "WvDZ4qFBR6DGGOMNt"
      )
      .then(
        (result) => {
          console.log(result.text);
          alert("You data is submmited succesfully");
        },
        (error) => {
          console.log(error.text);
          console.warn(
            "i ma not able to sent your data please try again and check the email id"
          );
        }
      );
    setcontact("");
    settextarea("");
    setname("");
    setemail("");
  };

  return (
    <div className="contact mt-5 pt-4" id="contact ">
      <h1 className="stroke-text text-center">
        <i> Contact Me</i>
      </h1>
      <p className="text-info fs-5 text-center">what's Next ?</p>
      <hr
        className={`text-${props.mode === "light" ? "dark" : "light"} my-4`}
      />
      <div className="text-center">
        <h1
          className={`f-bold text-${props.mode === "light" ? "dark" : "light"}`}
        >
          Get In Touch
        </h1>
        <div>
          <p className={`text-${props.mode === "light" ? "dark" : "light"}`}>
            I am available to work on your projects and bring your ideas to
            life. I am just a click away.
          </p>
        </div>
      </div>
      <div style={{ flexWrap: "wrap" }} className="main d-flex mt-5">
        <div className="form-left ">
          <div className="mt-4">
            <i
              className={` text-${
                props.mode === "light" ? "dark" : "light"
              } fs-3   uil uil-map-marker`}
            />
            <span className="text-white">
              <a
                className="text-decoration-none"
                href="https://www.google.com/maps/place/Aliah+University/@22.5894397,88.4845858,17z/data=!3m1!4b1!4m5!3m4!1s0x3a0275a57df0d8fb:0x3d6f3c9f28c20ab1!8m2!3d22.5894397!4d88.4867745"
                target="_blank"
                rel="noopener noreferrer"
              >
                New Town Kolkata,West Bengal 700156
              </a>
            </span>
          </div>
          <div className="mt-4">
            <i
              className={`uil fs-3 text-${
                props.mode === "light" ? "dark" : "light"
              } rounded-circle uil-envelope-alt`}
            />
            <a
              target="_blank"
              rel="noreferrer"
              className="text-decoration-none "
              href="mailto:shoyebmd424@gmail.com"
            >
              shoyebmd424@gmail.com
            </a>
          </div>
          <div className="mt-4">
            <i
              className={`uil fs-3 text-${
                props.mode === "light" ? "dark" : "light"
              } rounded-circle uil-phone-alt`}
            ></i>
            <a
              target="_blank"
              className="text-decoration-none"
              href="http://tel:+91-987-719-04481"
              rel="noopener noreferrer"
            >
              +91-9871904481
            </a>
          </div>
        </div>
        <form
          ref={form}
          onSubmit={sendEmail}
          className="col-lg-7 col-11 ms-auto"
        >
          <div>
            <div className="mb-3 flex-1">
              <input
                style={border}
                onChange={ename}
                placeholder="Full Name"
                value={name}
                required
                type="text"
                className=" text-info bor-radius form-control"
                id="name"
                aria-describedby="nameHelp"
              />
            </div>
            <div className="d-flex ">
              <div className="mb-3 w-50">
                <input
                  style={border}
                  onChange={eemail}
                  required
                  placeholder="Email"
                  value={email}
                  type="email"
                  name="email"
                  className="  text-info  bor-radius form-control"
                  id="exampleInputEmail1"
                  aria-describedby="emailHelp"
                />
              </div>
              <div className="mb-3  ms-4 w-50">
                <input
                  style={border}
                  onChange={econtact}
                  required
                  placeholder="Phone Number"
                  value={contact}
                  name="contact"
                  type="tel"
                  className=" text-info  bor-radius form-control"
                  id="contact"
                />
              </div>
            </div>
          </div>
          <div>
            <textarea
              style={border}
              onChange={etextarea}
              required
              className="w-100 p-3  text-info bor-radius"
              name="textarea"
              value={textarea}
              id="desc"
              cols="30"
              rows="8"
            ></textarea>
          </div>
          <button
            type="submit"
            className="btn mt-4 px-5 py-2 border border-info btn-outline-info"
          >
            Submit
          </button>
        </form>
      </div>
    </div>
  );
}

export default Contact;
