import { useState } from "react";
import { Link } from "react-scroll";
import logo from "../../component/images/logos-removebg-preview.png";
import "./Navbar.css";
function Navbar(props) {
  const [open, setopen] = useState(false);

  return (
    <>
      <nav
        id="home"
        className={`navbar mx-2 fixed-top navbar-expand-lg navbar-${props.mode} `}
      >
        <div className="container-fluid">
          <Link className="navbar-brand" to="/">
            <img style={{ width: "3rem" }} src={logo} alt="loading" />
          </Link>
          <i
            style={{ fontSize: "25px" }}
            onClick={() => setopen(!open)}
            className={`uil show toggle text-${
              props.mode === "dark" ? "light" : "dark"
            } uil-align-justify`}
          ></i>
          <div
            className={open ? " navattack  h5 " : "content"}
            id="navbarSupportedContent"
          >
            <i
              onClick={() => setopen(!open)}
              className={
                open
                  ? "uil toggle uil-multiply text-white cross"
                  : "d-none cross "
              }
            />
            <div className={open ? "brand" : "d-none"}>
              <Link className="navbar-brand mb-2" to="/">
                <img style={{ width: "3rem" }} src={logo} alt="loading" />
              </Link>
              <hr className="bg-dark" />
            </div>
            <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
              <li className="nav-item px-md-5">
                <span
                  data-bs-toggle="tooltip"
                  data-bs-placement="top"
                  title="Home"
                >
                  <Link
                    to="app"
                    spy={true}
                    smooth={true}
                    offset={50}
                    duration={500}
                    className="nav-link  "
                    activeclassname="menu_active"
                    href="/"
                  >
                    <h4> Home</h4>
                  </Link>
                </span>
              </li>
              <li className="nav-item px-md-5">
                <span
                  data-bs-toggle="tooltip"
                  data-bs-placement="top"
                  title="Project"
                >
                  <Link
                    to="project"
                    spy={true}
                    smooth={true}
                    offset={50}
                    duration={500}
                    className="nav-link "
                    activeclassname="menu_active"
                    href="/project"
                  >
                    <h4> Project</h4>
                  </Link>
                </span>
              </li>
              <li className="nav-item px-md-5">
                <span
                  data-bs-toggle="tooltip"
                  data-bs-placement="top"
                  title="About"
                >
                  <Link
                    to="about"
                    spy={true}
                    smooth={true}
                    offset={50}
                    duration={500}
                    className="nav-link "
                    activeclassname="menu_active"
                    href="/about"
                  >
                    <h4> About</h4>
                  </Link>
                </span>
              </li>
              <li className="nav-item px-md-5 ">
                <span
                  data-bs-toggle="tooltip"
                  data-bs-placement="top"
                  title="Contact"
                >
                  <Link
                    to="contact"
                    spy={true}
                    smooth={true}
                    offset={50}
                    duration={500}
                    className="nav-link "
                    activeclassname="menu_active"
                    href="/contact"
                  >
                    <h4> Contact</h4>
                  </Link>
                </span>
              </li>
              <li
                className={`form-check mt-2 form-switch text-${
                  props.mode === "light" ? "dark" : "light"
                }`}
              >
                {/* <input onClick={props.togglemode} style={{cursor:"pointer"}}
                  className=" form-check-input"
                  type="checkbox"
                  role="switch"
                  id="dark"
                /> */}
                <i
                  onClick={props.togglemode}
                  className={
                    props.mode === "light" ? "d-none" : "uil fs-3  uil-sun"
                  }
                  style={{ cursor: "pointer" }}
                />
                <i
                  onClick={props.togglemode}
                  className={
                    props.mode === "dark" ? "d-none" : "uil fs-3  uil-moon"
                  }
                  style={{ cursor: "pointer" }}
                />
                {/* <label className=" form-check-label" style={{textTransform:"capitalize"}} htmlFor="dark">
                {props.mode==='light'?"dark":"light"} Mode
                </label> */}
              </li>
            </ul>
          </div>
        </div>
      </nav>
    </>
  );
}

export default Navbar;
