import React from "react";
import profile from "../images/profile.png";
import java from "../images/java.png";
import mysql from "../images/mysql.png";
import Skills from "./Skills";
function About(props) {
  return (
    <div className="about mt-5" id="about">
      <h1 style={{ textTransform: "uppercase" }} className="stroke-text">
        <i> About Me</i>
      </h1>
      <hr
        className={`text-${props.mode === "light" ? "dark" : "light"} my-4`}
      />
      <div className="d-flex" style={{ flexWrap: "wrap" }}>
        <div className="col-12 col-lg-6">
          <div className={`text-${props.mode === "light" ? "dark" : "light"}`}>
            Hello! <br /> My name is
            <strong className="text-info"> Md Shoyeb </strong> and I enjoy
            creating things that live on the internet. My interest in Software
            Development / Web Developement.
          </div>
          <div
            className={`text-${props.mode === "light" ? "dark" : "light"} mt-3`}
          >
            Recently I have completed my B.Tech(Bechelor of Technology) Degree
            in Electronics and communication engineering(ECE) from
            <strong className="text-info">
              <i> Aliah University </i>
            </strong>
            , New Town Kolkata in 2023. i am looking a Job opportunity on my
            skills, also my diploma engineering (ECE) is completed from
            <strong className="text-info"> Jamia Millia Islamia </strong>
            University in 2020.
          </div>
          <div
            className={`mt-3 text-${props.mode === "light" ? "dark" : "light"}`}
          >
            I also recently launched a E Commerce App with
            <strong className="text-info"> MERN Stack</strong>.
          </div>
        </div>

        <div className="pic">
          <div style={{ position: "relative" }} className=" mx-5 picture">
            <div className="position-relative " style={{ zIndex: "5" }}>
              <img
                className={`shadow bor-radius bg-${
                  props.mode === "light" ? "dark" : "light"
                }`}
                typeof="image/png"
                src={profile}
                style={{ width: "20rem", zIndex: "1", height: "20rem" }}
                alt=""
              />
            </div>
            <div
              className="empty bor-radius"
              style={{
                width: "20rem",
                height: "20rem",
                position: "absolute",
                zIndex: "2",
                left: "1rem",
                top: "1rem",
                border: "2px solid cyan",
              }}
            ></div>
          </div>
        </div>
      </div>
      <h1 className="text-info text-center mt-5 pt-4">Skills</h1>
      <p
        className={`text-center text-${
          props.mode === "light" ? "dark" : "light"
        }`}
      >
        Here are a few technologies Ive been working with recently:
      </p>
      <hr
        className={`text-${props.mode === "light" ? "dark" : "light"} my-4`}
      />
      <div>
        <Skills />
      </div>
    </div>
  );
}

export default About;
