import React from "react";

const Skills = () => {
  return (
    <>
      {skills.map((data, skey) => (
        <button
          key={skey}
          className="bg-white text-info border-0 rounded text-capitalize m-3 shad"
        >
          <h4>{data.s}</h4>
        </button>
      ))}
    </>
  );
};

export default Skills;

const skills = [
  { s: "Data Structure and Alogorithm" },
  { s: "Design and Analysis of algorithms" },
  { s: "Java" },
  { s: "JavaScript" },
  { s: "React Js" },
  { s: "Node Js" },
  { s: "Express Js" },
  { s: "Bootstrap" },
  { s: "Spring Framework" },
  { s: "Spring Boot" },
  { s: "MySQL" },
  { s: "MongoDB" },
  { s: "HTML&CSS" },
  { s: "Object Oriented Programming" },
  { s: "Networking" },
  { s: "RDBMS" },
];
