import React from "react";
import { Carousel } from "react-bootstrap";
const Slide = ({ images }) => {
  return (
    <>
      <Carousel>
        {images.map((img) => (
          <Carousel.Item interval={1000}>
            <img className="d-block w-100" src={img.img} alt="First slide" />
          </Carousel.Item>
        ))}
      </Carousel>
    </>
  );
};

export default Slide;
