import React from "react";
import { Data } from "../data/Data";
import Slide from "./Slide";

const ProjectT = ({ mode }) => {
  return (
    <>
      <h1 className=" stroke-text mt-5 text-center pt-3">
        <i>I've Built Projects </i>
      </h1>
      <p
        className={`text-center animate text-${
          mode === "dark" ? "light" : "dark"
        }`}
      >
        see more projects click on{" "}
        <a
          target="_blank"
          rel="noreferrer"
          style={{ textDecoration: "none" }}
          className="text-info"
          href="https://github.com/shoyebmd424/"
        >
          Github
        </a>
      </p>
      <hr className={`text-${mode === "light" ? "dark" : "light"} my-4`} />
      <div className="container-fluid">
        <div className="row">
          <div className="row row-cols-md-2 row-cols-lg-3  ">
            {Data.map((item, pk) => (
              <div className="my-2">
                <div
                  className={`project rounded bg-${
                    mode === "dark" ? "light" : "dark"
                  } p-3`}
                >
                  {/* <img src="p-images/araqsa-1.png" alt="" srcset="" /> */}
                  <Slide className="slide" images={item.images} />
                  <h3 className="text-center my-1 text-info text-uppercase">
                    {item.name}
                  </h3>
                  <div
                    className={`card-body text-${
                      mode === "dark" ? "dark" : "light"
                    }`}
                  >
                    <p>
                      {item.desc}
                      <a
                        target="_blank"
                        rel="noreferrer"
                        style={{ textDecoration: "none" }}
                        className="text-info"
                        href={`${item.a}`}
                      >
                        Project Link
                      </a>
                      .
                    </p>
                    <h5>Technologies:-</h5>
                    <div className="d-flex flex-wrap">
                      {item.skills.map((i, key) => (
                        <li
                          className={`text-uppercase `}
                          style={{ listStyle: "none", padding: "0.5rem" }}
                        >
                          {i.s}
                        </li>
                      ))}
                    </div>
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>
    </>
  );
};

export default ProjectT;
