import React from "react";

const Service = ({ mode }) => {
  return (
    <>
      <h1 className=" stroke-text mt-5 text-center pt-3">
        <i> Provide Services </i>
      </h1>
      <p
        className={`text-center animate text-${
          mode === "dark" ? "light" : "dark"
        }`}
      >
        These are the services I have provided for the client.
      </p>
      <hr className={`text-${mode === "light" ? "dark" : "light"} my-4`} />
      <div className="container-fluid">
        <div className="row">
          <div className="row row-cols-md-3 ">
            {data.map((item, key) => (
              <div className="cards p-2">
                <div
                  className={`card bg-${mode === "dark" ? "light" : "dark"}`}
                >
                  <div className="card-body text-center">
                    {item.icons}
                    <h2 className="text-info" style={{ fontWeight: "bold" }}>
                      {item.topic}
                    </h2>
                    <p className={`text-${mode === "dark" ? "dark" : "light"}`}>
                      {item.desc}.
                    </p>
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>
    </>
  );
};

export default Service;
export const data = [
  {
    icons: <i class=" text-info uil uil-globe fs-1 "></i>,
    topic: "Web Development",
    desc: "Make website reliable, secure and robust through the MERN Stack, Java",
  },
  {
    icons: <i class=" text-info uil uil-android fs-1"></i>,
    topic: "Apps Development",
    desc: " I can build the mobile app in native and hybrid technology with admin panel",
  },
  {
    icons: <i class=" text-info uil uil-cloud-computing fs-1"></i>,
    topic: "Website Deployment",
    desc: "Put your website on cloud with Github action , setup virtual private server(VPS) server",
  },
];
